



















import { Component, Vue,Prop } from 'vue-property-decorator';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { ApexChartOptions, ApexChartSeries } from '@/modules/charts/interfaces/apexchart.interface';

@Component({
  components: {
      SvgIcon
  },
})
export default class DataCard extends Vue { 
    @Prop() title!: string;
    @Prop() data?: {
        data: string | number,
        big?: boolean
    };
    @Prop() styles!: string;
    @Prop() small?: boolean;
    @Prop() img?: {
        source: string,
        alt: string,
        svg: boolean,
    }
    @Prop() chart?:{
        show: boolean,
        type: string,
        options: any,
        series: any,
    }

    get dataClass(){
      let classes = '';
      if (!this.small) {
        classes ='mt-2 text-end ';
        if (this.data.big) classes += 'fs-biggest'
        else classes += 'fs-bigger fw-bold'
      }
      else classes = 'fs-bigger fw-bold'
      return classes
    }

}
