















































import { Component, Vue } from 'vue-property-decorator';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import NextLevelSection from '@/modules/client/components/profile/NextLevelSection.vue';
import FrequentBeneficiariesSection from '@/modules/client/components/profile/FrequentBeneficiariesSection.vue';
import AmbassatorLinkSection from '@/modules/client/components/profile/AmbassatorLinkSection.vue';
import ProfileSection from '@/modules/client/components/profile/ProfileSection.vue';
import UserDataSection from '@/modules/client/components/profile/UserDataSection.vue';
import AuthMethods from '@/store/auth/methods/auth.methods';
import VerifLevelMethods from '@/store/verificationLevels/methods/verificationLevels.methods';
import {auth, verifLevels} from '@/store/namespaces'
import { User } from '@/modules/auth/interfaces/user.interface';
import DataCard from '@/components/utilities/DataCard.vue';
import {getCountry} from 'country-list-spanish';
import { getStatusColor, getStatusIcon, getStatusFemaleName } from '@/modules/verificationLevels/functions/verifLevels.functions';
import { WholeSalePartnerReq } from '@/modules/verificationLevels/interfaces/requirement.interface';

@Component({
  components: {
      SvgIcon,
      NextLevelSection,
      FrequentBeneficiariesSection,
      AmbassatorLinkSection,
      ProfileSection,
      UserDataSection,
      DataCard
  },
})
export default class Profile extends Vue {

    residCountryInSpanish: string = '';
    natCountryInSpanish: string = '';
    wholeSalePartnerRequestStatus : boolean | number = 0;
    chartSeries = [{
                name: 'series1',
                data: [31, 20 , 97, 120],
            }
    ]
    chartOptions = {
            fill: {
            colors: ['#848fdf']
            },
            colors:['#6573d7']
            ,
            grid: {
                yaxis: {
                    lines: {
                        show: false
                    }
                }
            },
            chart: {
              type: 'area',
              toolbar:{
                show: false
              }
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              curve: 'smooth'
            },
            xaxis: {
              labels:{
                  show: false,
              }
            },
            legend:{
                show: false
            },
            yaxis:{
                show: false
            },
            tooltip: {
              x: {
                format: 'dd/MM/yy HH:mm'
              },
            },
        }

    async mounted(){
        this.residCountryInSpanish = await getCountry(this.userData.iso_code_resid_country)
        this.natCountryInSpanish = await getCountry(this.userData.iso_code_nationality_country)
        await this.fetchWholeSalePartnerReqs(this.userData.email_user)
        if (this.wholeSalePartnerReqs.questions === null)
            this.wholeSalePartnerRequestStatus = 0;
        else{
            this.wholeSalePartnerRequestStatus = true;
            for (let i=0;i<this.wholeSalePartnerReqs.questions.length;i++){
                if (this.wholeSalePartnerReqs.questions[i].question_apb_ok === false || 
                    this.wholeSalePartnerReqs.questions[i].question_apb_ok === null){
                    this.wholeSalePartnerRequestStatus = this.wholeSalePartnerReqs.questions[i].question_apb_ok;
                    break;
                }
            }
        }
    }

    get badgeName(){
        if (typeof this.wholeSalePartnerRequestStatus === 'number') return null
        else {
            return getStatusFemaleName(this.wholeSalePartnerRequestStatus)
        }
    }

    get badgeColor(){
        if (typeof this.wholeSalePartnerRequestStatus === 'number') return null
        else {
            return getStatusColor(this.wholeSalePartnerRequestStatus)
        }
    }

    get badgeIcon(){
       if (typeof this.wholeSalePartnerRequestStatus === 'number') return null
        else {
            return getStatusIcon(this.wholeSalePartnerRequestStatus)
        }
    }

    @auth.Getter(AuthMethods.getters.GET_USER_DATA)
        userData! : User;  
    @verifLevels.Action(VerifLevelMethods.actions.FETCH_WHOLESALEPARTNER_REQUIREMENTS)
    fetchWholeSalePartnerReqs!: (emailUser: string) => Promise<boolean>
    @verifLevels.Getter(VerifLevelMethods.getters.GET_WHOLESALEPARTNER_REQUIREMENTS)
    wholeSalePartnerReqs!: {questions: WholeSalePartnerReq[], email_user: string}

}
